const axios = require('axios').default;

const url = '185.141.110.85';
const token = localStorage.getItem('token');
const agent = localStorage.getItem('agent');
const port = '4444';

const date = new Date();
const day = date.getDate();
const month = date.getMonth() + 1;
const year = date.getFullYear();

const addZero = val => {
  if (val < 0) {
    return '0' + val;
  } else {
    return val;
  }
};
const param = year + '-' + addZero(month) + '-' + addZero(day);
//login
///mjhnkh
export const getUserAuth = async param => {
  const response = await axios.post(`http://${url}:${port}/login`, param);

  return response;
};

