export const getTotalDuration = async () => {
  let query = `with recursive cte1 as(select 
    call_id,
    to_char(dst_start_time at time zone 'utc' at time zone 'Etc/GMT+3','dd-mm-yyyy HH24:MI:SS') as dst_start_time,
    to_char(dst_end_time at time zone 'utc' at time zone 'Etc/GMT+3','dd-mm-yyyy HH24:MI:SS') as dst_end_time,
    dst_end_time-dst_start_time as duration,
  src_display_name,
     
    case when src_dn_type = 1 then src_display_name else src_dn end as caller,
    case when dst_dn_type = 1 then dst_display_name else dst_dn end as called
  
  from  cl_segments_view_new 
     where 
    --src_dn_type = 0 and dst_dn_type = 1
    --or
    src_dn_type = 1 and dst_dn_type = 0
    and
    (src_dn = '${agent}' or dst_dn = '${agent}')
    and
    act != 10
    and
    act != 1
    and  cl_segments_view_new.dst_start_time >= date_trunc('day',timestamp '${param}'-interval '24' hour)
    and
    dst_answer_time is not null
    and
    (dst_dn_type = 0 or dst_dn_type = 1)
    order by cl_segments_view_new.start_time desc),
    cte2 as(select 
    call_id,
    to_char(dst_start_time at time zone 'utc' at time zone 'Etc/GMT+3','dd-mm-yyyy HH24:MI:SS') as dst_start_time,
    to_char(dst_end_time at time zone 'utc' at time zone 'Etc/GMT+3','dd-mm-yyyy HH24:MI:SS') as dst_end_time,
    dst_end_time-dst_start_time as duration,
  src_display_name,
     
    case when src_dn_type = 1 then src_display_name else src_dn end as caller,
    case when dst_dn_type = 1 then dst_display_name else dst_dn end as called
  
  from  cl_segments_view_new 
     where 
    src_dn_type = 0 and dst_dn_type = 1
    --or
    --src_dn_type = 1 and dst_dn_type = 0
    and
    (src_dn = '${agent}' or dst_dn = '${agent}')
    and
    act != 10
    and
    act != 1
    and  cl_segments_view_new.dst_start_time >= date_trunc('day',timestamp '${param}'-interval '24' hour)
    and
    dst_answer_time is not null
    and
    (dst_dn_type = 0 or dst_dn_type = 1)
    order by cl_segments_view_new.start_time desc), cte3 as(select coalesce(sum(cte1.duration),'0')  from cte1 union all select coalesce(sum(cte2.duration),'0') from cte2)
    select coalesce(sum(cte3.coalesce),'0') as total from cte3`;
  const response = await axios.post(
    `http://${url}:${port}/queryresult`,
    { token: `${token}`, query: `${query}` },
    {
      headers: {
        'Content-Type': 'application/json',
      },
    },
  );
  return response;
};
