export const getInternalsSummaryLongestCaller = async () => {
  let string = `with recursive cte1 as (
    select 
    *
    from
    cl_segments_view
    where start_time >= date_trunc('day',timestamp '${param}')
    and act!= 1
    and start_time <= date_trunc('day',timestamp '${param}' + interval '1 day')
  )
  select 
  src_display_name as en_uzun_arayan_numara,
  cast(dst_end_time-dst_answer_time as character varying) as en_uzun_aranan_süre
  from
  cte1
  where
  cte1.dst_dn ='${agent}'  
  and
  dst_answer_time is not null

  order by (dst_end_time-dst_answer_time) desc
  limit 5`;

  return axios.post(`http://${url}:${port}/queryresult`, {
    token: localStorage.getItem('token'),
    query: string,
  });
};

