export const getServiceLevel = async () => {
  const query = `select 
  count(reason_noanswerdesc),
  reason_noanswerdesc
  from  
  callcent_ag_dropped_calls 
  where 
  time_start > '${param} 00:00:00'
  and 
  ag_num = '${agent}'
  group by reason_noanswerdesc
  union
  select 
  count(distinct(call_history_id)) as counts,
  to_dn
  from
  callcent_queuecalls_view
  where 
  time_start >= '${param} 00:00:00' 
  and to_dn = '${agent}'
  and ring_time > interval '0 seconds' and ring_time < interval '20 seconds'
  group by to_dn
  `;
  const response = await axios.post(
    `http://${url}:${port}/queryresult`,
    { token: `${token}`, query: `${query}` },
    {
      headers: {
        'Content-Type': 'application/json',
      },
    },
  );
  return response.data;
};

