import React, { useState, useEffect } from 'react';
import { Card, CardActions, FormControl, InputLabel, Select, MenuItem, FormHelperText, Grid } from '@material-ui/core';
import GridContainer from '@jumbo/components/GridContainer';
import DropdownMenu from 'routes/Pages/AgentStatuses/Dropdown';
import IntlMessages from '@jumbo/utils/IntlMessages';
import * as api from 'Api/api';

const QueueFilter = ({
  queues,
  chosen,
  setChosen,
  value,
  setValue,
  setOpen,
  response,
  setLastAction,
  lastAction,
  setResponse,
  setPeriod,
  isChecked,
  breakname,
  setBreak,
  setChecked,
  online,
  setOnline,
}) => {
  const handleChange = event => {
    setValue(event.target.value);
  };
  const handleTagChange = event => {
    setChosen(event.target.value);
  };
  const [department, setDepartment] = useState([]);
  useEffect(() => {
    api.showTags().then(resp => setDepartment([...JSON.parse(resp.data)]));
  }, []);

  return (
    <Card>
      <CardActions>
        <GridContainer>
          <Grid item xs={4} md={4} lg={4}>
            <FormControl sx={{ m: 1, minWidth: 120 }}>
              <InputLabel id="dl">Department</InputLabel>
              <Select labelId="dl" id="dl" value={chosen} label="Department" onChange={handleTagChange}>
                {department.length > 0 &&
                  department.map((value, index) => (
                    <MenuItem key={index} value={value.department}>
                      {value.department}
                    </MenuItem>
                  ))}
              </Select>
              <FormHelperText>Örnek olarak verilmiştir. Sonradan değişiklik yapılacaktır</FormHelperText>
            </FormControl>
          </Grid>

          {queues.length > 0 ? (
            <>
              <Grid item xs={4} md={4} lg={4}>
                <FormControl sx={{ m: 1, minWidth: 120 }}>
                  <InputLabel id="demo-simple-select-helper-label">
                    {' '}
                    <IntlMessages id={'select.queue'} />
                  </InputLabel>
                  <Select
                    labelId="demo-simple-select-helper-label"
                    id="demo-simple-select-helper"
                    value={value}
                    label="Age"
                    onChange={handleChange}>
                    {queues.length > 0 &&
                      queues.map((value, index) => (
                        <MenuItem key={index} value={value.queue_dn}>
                          {value.queue_dn} {value.queue_display_name}
                        </MenuItem>
                      ))}
                  </Select>
                  <FormHelperText>
                    <IntlMessages id={'select.warning'} />
                  </FormHelperText>
                </FormControl>
              </Grid>
            </>
          ) : (
            ''
          )}

          <Grid item xs={4} md={4} lg={4}>
            <DropdownMenu
              isChecked={isChecked}
              online={online}
              breakname={breakname}
              setOnline={setOnline}
              setChecked={setChecked}
              setOpen={setOpen}
              lastAction={lastAction}
              setLastAction={setLastAction}
              setPeriod={setPeriod}
              setBreak={setBreak}
              response={response}
              queues={queues}
              setResponse={setResponse}
            />
          </Grid>
        </GridContainer>
      </CardActions>
    </Card>
  );
};

export default QueueFilter;
