import React, { useEffect } from 'react';
import { makeStyles, lighten } from '@material-ui/core/styles';
import {
  TableCell,
  TableContainer,
  Table,
  TableBody,
  TableHead,
  Paper,
  TableRow,
  IconButton,
  AppBar,
  Toolbar,
  Menu,
  MenuItem,
} from '@material-ui/core';
import * as api from 'Api/api';
import { NotificationContainer, NotificationManager } from 'react-notifications';
import TagSelection from './TagSelection';
import { PlayArrow, Clear, FilterList } from '@material-ui/icons';
import IntlMessages from '@jumbo/utils/IntlMessages';
import Note from './Note';
import RatingBoard from './Rating';

const DataTable = ({ data, chosen, setResponse }) => {
  const useStyles = makeStyles(theme => ({
    root: {
      backgroundColor: lighten(theme.palette.background.paper, 0.1),
      fontFamily: 'Open Sans',
    },
    table: {
      minWidth: 200,
      boxShadow: 3,
      border: '1px',
    },
    tableHead: {
      fontWeight: 'bolder',
      fontSize: 14,
      color: theme.palette.common.white,
      backgroundColor: theme.palette.background.default,
    },
    footer: {
      color: theme.palette.common.white,
      backgroundColor: theme.palette.background.default,
      fontWeight: 'bolder',
    },
    drawer: {
      position: 'relative',
      marginLeft: 'auto',
      width: 200,
      '& .MuiBackdrop-root': {
        display: 'none',
      },
      '& .MuiDrawer-paper': {
        width: 200,
        position: 'absolute',
        minHeight: 100,
        transition: 'none !important',
      },
    },
  }));

  const [tags, setTags] = React.useState([]);
  const [msg, setMsg] = React.useState([]);
  let [path, setPath] = React.useState('');

  useEffect(() => {
    api.getRecordingPath().then(res => {
      var str = res.data.data.toString();
      setPath(str.substring(str.indexOf(':') + 2, str.lastIndexOf('"')));
      // console.log(str.substring(str.indexOf(':') + 2, str.lastIndexOf('"')));
    });
  }, []);

  const listenRecord = dir => {
    api
      .getRecording(path, dir)
      .then(resp => {
        window.location.href = resp.request.responseURL;
      })
      .catch(err => NotificationManager.error('No Recording', '', 3000));
  };

  useEffect(() => {
    api.tagMessage(chosen).then(resp => setMsg([...JSON.parse(resp.data)]));
  }, [chosen]);
  useEffect(() => {
    api.getTags().then(resp => setTags([...JSON.parse(resp.data)]));
  }, []);

  const classes = useStyles();
  const [open, setOpen] = React.useState(false);
  const handleClose = () => {
    setOpen(false);
    const filtered = data.filter(function(a) {
      return !tags.some(function(b) {
        return b.call_id === a.call_id;
      });
    });
    setResponse(filtered);
  };
  const handleAll = () => {
    setOpen(false);
    api.getPreviousCalls().then(resp => {
      if (typeof resp !== undefined) {
        setResponse([...JSON.parse(resp.data)]);
      }
    });
  };

  return (
    <>
      <NotificationContainer />
      <TableContainer component={Paper} className={classes.root}>
        <AppBar position="static">
          <Toolbar style={{ display: 'flex' }}>
            <IconButton
              onClick={() => {
                setOpen(!open);
              }}
              style={{ marginLeft: 'auto' }}
              color="inherit"
              aria-label="filterButton">
              <FilterList />
            </IconButton>
          </Toolbar>
        </AppBar>

        <Menu open={open} onClose={handleClose} className={classes.drawer}>
          <MenuItem onClick={() => handleClose()}>
            <IntlMessages id={'no.tagged'} />
          </MenuItem>
          <MenuItem onClick={() => handleAll()}>
            <IntlMessages id={'all'} />
          </MenuItem>
        </Menu>

        <Table className={classes.table} size="small" aria-label="a dense table">
          <caption>
            <IntlMessages id={'table.note'} />
          </caption>
          <TableHead component={'thead'} className={classes.tableHead}>
            <TableRow style={{ fontFamily: 'Open Sans' }}>
              <TableCell align="center">
                <IntlMessages id={'caller'} />
              </TableCell>
              <TableCell align="center">
                <IntlMessages id={'called'} />
              </TableCell>
              <TableCell align="center">
                <IntlMessages id={'start.time'} />
              </TableCell>
              <TableCell align="center">
                <IntlMessages id={'talking.time'} />
              </TableCell>
              <TableCell align="center">
                <IntlMessages id={'ringing.time'} />
              </TableCell>
              <TableCell align="center">
                <IntlMessages id={'end.time'} />
              </TableCell>
              <TableCell align="center">
                {' '}
                <IntlMessages id={'record'} />
              </TableCell>
              <TableCell width={'200px'} align="center">
                <IntlMessages id={'rating'} />
              </TableCell>
              <TableCell align="center">
                {' '}
                <IntlMessages id={'notes'} />
              </TableCell>
              <TableCell align="center">
                <IntlMessages id={'tag'} />
              </TableCell>
            </TableRow>
          </TableHead>

          <TableBody>
            {data.map((row, i) => (
              <TableRow key={i}>
                <TableCell align="center">{row.caller}</TableCell>
                <TableCell align="center">{row.called}</TableCell>
                <TableCell align="center">{row.dst_start_time}</TableCell>
                <TableCell align="center">{row.talking_time}</TableCell>
                <TableCell align="center">{row.ringing_time}</TableCell>
                <TableCell align="center">{row.dst_end_time}</TableCell>
                <TableCell align="center">
                  {row.coalesce !== 'no recording' ? (
                    <IconButton
                      onClick={() => listenRecord(row.coalesce)}
                      style={{ backgroundColor: '#C6D57E', color: '#406343' }}
                      size="small"
                      aria-label="listen">
                      <PlayArrow />
                    </IconButton>
                  ) : (
                    <IconButton
                      disabled
                      style={{ backgroundColor: '#FE8F8F', color: '#FF5C58' }}
                      size="small"
                      aria-label="listen">
                      <Clear />
                    </IconButton>
                  )}
                </TableCell>
                <TableCell align="center">{tags.length > 0 ? <RatingBoard tags={tags} id={row.call_id} /> : ''}</TableCell>
                <TableCell align="center">
                  {tags.length > 0 ? <Note setMsg={setMsg} id={row.call_id} tags={tags} /> : ''}
                </TableCell>
                <TableCell align="center">
                  {chosen !== '' && tags.length > 0 ? (
                    <TagSelection msg={msg} id={row.call_id} tags={tags} chosen={chosen} />
                  ) : (
                    ''
                  )}
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </>
  );
};

export default DataTable;
